html,body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to bottom right, rgb(26, 3, 46), black);
  color: white;
  background-size: cover; 
  background-repeat: no-repeat;
  height: 150%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
  background-color: black;
  color: rgb(255, 255, 255);
  padding: 5px 5px;
  display: flex;
  transition: 0.3s;
}
.navbar:hover{
  box-shadow: 0px 1px 30px rgba(233, 198, 255, 0.381);
}
.navbar h2 {
  margin: 0;
  margin-right: 30px;
}
.navbar li {
  display: inline-block;
  margin: 7px 20px;
  margin-left: 30px;
  transition: 0.3s;
}
.navbar li:hover{
  color: rgb(241, 202, 255);
  
}
.navbar li:last-child {
  margin-right: 0;
}
.navbar ul{
  display: flex;
}
.navbar a {
  text-decoration: none;
  color: inherit; 
}
.container{
  align-items: center;
  container:inherit;
  display:inline;
  position: relative;
  left: 28%;

}
.but{
  background: linear-gradient(to bottom right, rgb(48, 5, 86), black);
  display: inline;
  margin: 10px 0px;
  color: rgb(255, 255, 255);
  border: 1px solid rgb(255, 255, 255);
  padding: 6px 10px;
  border-radius: 3px;
  transition: 0.1s;
  font-size: 15px;
  margin-right:10px ;
}
.but:hover{
  background: rgb(37, 9, 51);
  transform: scale(1.05);
  border: 0px ;
}
#mybox{
  border-radius: 0px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 
  'Lucida Grande', 'Lucida Sans Unicode', Geneva, 
  Verdana, sans-serif;
  background-color: black;
  border: 1px solid white;
  color: white;
  padding: 15px 15px;
  transition: 0.8s;
}
#mybox:hover{
  background-color: #18032b;
  color: rgb(206, 206, 206);
}

#summary{
  position: relative;
}
.preview{
  position: relative;
  border: 0.5px solid white;
  background-color:rgb(0, 0, 0) ;
  padding: 1px 10px 10px 20px;
  margin: 3px;
  width: 81vh;
  box-shadow: 1px 1px 15px rgba(272, 272, 272, 0.5);
  transition: 0.3s;
  border-radius: 9px;
}
.preview:hover{
  box-shadow: 1px 1px 25px rgba(272, 272, 272, 0.5);
  transform: scale(1.01);
  background-color:rgb(0, 0, 0) ;
}
.cont{
  margin: 50px 15px 15px 15px;
  padding: 15px 15px;
  width: 50%;
}
.switch {
  position: relative;
  display: inline-block;
  width: 54px;
  height: 28px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 28px;
}

.slider.round:before {
  border-radius: 50%;
}
@media screen and (max-width: 700px) {
  .navbar li {
    margin: 7px 0;
    display: inline-block;
  }

  .navbar ul {
    flex-direction: column;
    text-align: center;
  }

  .container {
    left: 0;
    margin: 20px;
  }

  .but {
    margin-right: 10px;
  }

  #mybox {
    width: 100%;
  }

  .preview {
    width: 100%;
  }

  .cont {
    margin: 15px 0;
  }
}
.sli{
  position: relative;
  left: 65%;
}